.section {
  margin: 10px 0px;
  background: white;
  box-shadow: rgb(221, 221, 221) 0px 0px 5px;
  grid-template-columns: 1fr;
  gap: 20px;
}

.section__body {
  padding: 20px;
}

.section__header {
  padding: 20px;
  margin: 0px;
  border-bottom: 1px solid rgb(221, 221, 221);
  display: grid;
  grid-template-columns: 1fr auto auto;
}



@media (max-width: 767px) {
  .section__header {
    display: grid;
    grid-template-columns: 1fr;
    align-items: center;
    gap: 20px;
  }
}

.section__title {
  font-size: 20px;
  margin: 0px;
}